export class Solution {
    hostDiv;
    constructor(id: string) {
        this.hostDiv = document.getElementById(id);
        const url = 'https://namjulee.github.io/post-production-harvard-gsd.github.com/'
        // window.open(url, '_blank');

        const iframe = document.createElement('iframe');
        iframe.src = url;
        iframe.width = '100%';
        iframe.height = '100%';
        iframe.style.border = 'none';
        iframe.style.background = '#fff';
        this.hostDiv.appendChild(iframe);
    }
    public destroy() {
        while (this.hostDiv.lastElementChild) {
            this.hostDiv.removeChild(this.hostDiv.lastElementChild);
        }
    }
}

